.card-container {
  background-color: #fff;
  margin-top: 20vh;
  margin-left: 700px;
  padding: 2em 4em 4em 4em;
	width: 300px;
	overflow: hidden;
	box-shadow: 0px 0px 15px -5px;
  text-align: center;
}

.image-container img {
	overflow: hidden;
	height: 223px;
  border-radius: 50%;
}
.card-content {
  margin: 1rem;
  margin-top: 0.5rem;
  text-align: center;
}
.btns button {
  border: none;
  border-radius: 15%;
  background-color: rgb(236, 232, 232);
  transition: .5rem;
  font-size: 15px;
  font-weight: 700;
  margin-top: 20px;
  margin-left: 20px;
  padding: 15px 30px;
}
.btns button:hover {
  background-color: rgb(83, 41, 122);
  color: #fff;
}

.likes {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  
}
.fa-heart {
  color: crimson;
  font-size: 30px;
}

.fa-instagram {
  color: blue;
  font-size: 30px;
}

@media screen and (max-width: 600px) {

  .card-container {
    margin-top: 10vh;
    margin-left: 40px;
    padding: 0;
    padding-top: 20px;
}
}